import { getDefaultOptions, request } from 'api/helpers';

const resource = 'tasks';

export const apiTaskDetail = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskUpdatePut = async (serviceUrl, task) => {
  const url = `${serviceUrl}/${resource}/${task.id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: task ? JSON.stringify(task) : null,
  };
  return request(url, options);
};

