import { Box, Grid, LinearProgress, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { progressBar, priorityStyle, progressColor } from './style/statusStyle';
import TaskManager from '../../context/task/TaskManager';

const Status = ({ priority }) => {
  const { t } = useTranslation();
  const cssPriority = priorityStyle();
  const cssProgressBar = progressBar();
  const { task, metadata } = TaskManager();
  const cssColor = progressColor();

  const getProgressClass = progressNum => {
    return Math.ceil(progressNum / 5) * 5;
  };

  const progressClass = getProgressClass(task.completionPercentage);

  return (
    <Grid container spacing={1} alignItems="center">
      <Grid item xs={9}>
        <Box alignItems="center">
          <Box minWidth={35}>
            <Box
              display="flex"
              flexDirection="row"
              flexWrap="nowrap"
              justifyContent="space-between"
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.detail.progress')}
              </Typography>
              <Typography variant="subtitle2" color="textSecondary">
                {`${Math.round(task.completionPercentage || 0)}%`}
              </Typography>
            </Box>
          </Box>
          <Box width="100%" mr={1}>
            <LinearProgress
              classes={cssProgressBar}
              className={`${
                task.completionPercentage >= 0
                  ? cssColor[`perc${progressClass}`]
                  : cssColor.percNegative
              }`}
              variant="determinate"
              valueBuffer={100}
              value={task.completionPercentage}
            />
          </Box>
        </Box>
      </Grid>

      <Grid item xs={3}>
        <Box
          display="flex"
          flexDirection="row"
          alignItems="center"
          flexWrap="nowrap"
          justifyContent="space-between"
          marginLeft={1}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.detail.priority')}
          </Typography>
          <div className={`${cssPriority.priority} ${cssPriority[priority]}`}>
            {t(`entities.detail.${priority}`)}
          </div>
        </Box>
      </Grid>
    </Grid>
  );
};

Status.propTypes = {
  priority: PropTypes.string.isRequired,
};

export default Status;
