import { IconButton } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import CancelIcon from '@material-ui/icons/Cancel';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import EditIcon from '@material-ui/icons/Edit';
import Config from 'config/config';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const AttachmentCodeInput = ({ attachmentCode, referenceId, onConfirm }) => {
  const { t } = useTranslation();

  const [state, setState] = useState({
    active: false,
    value: attachmentCode,
    error: false,
  });

  const onEditHandler = () => {
    setState(prevState => ({ ...prevState, active: true }));
  };

  const onCancelHandler = () => {
    setState(prevState => ({ ...prevState, active: false, error: false, value: attachmentCode }));
  };

  const onConfirmHandler = () => {
    setState(prevState => ({ ...prevState, active: false }));
    onConfirm(state.value);
  };

  const onChangeHandler = event => {
    if (event.target.value.toString().trim().length !== Config.LENGTH_TASK_ATTACHMENT_CODE) {
      setState(prevState => ({ ...prevState, error: true, value: event.target.value }));
    } else {
      setState(prevState => ({ ...prevState, error: false, value: event.target.value }));
    }
  };

  return (
    <>
      {state.active ? (
        <>
          <IconButton
            id="saveBtn"
            aria-label="save"
            onClick={onConfirmHandler}
            disableRipple
            disabled={state.error}
          >
            <CheckCircleIcon />
          </IconButton>
          <IconButton id="cancelBtn" aria-label="delete" onClick={onCancelHandler}>
            <CancelIcon />
          </IconButton>
          <TextField
            variant="outlined"
            error={state.error}
            type="text"
            id="code"
            name="name"
            value={state.value}
            inputProps={{ maxLength: Config.LENGTH_TASK_ATTACHMENT_CODE }}
            helperText={
              state.error
                ? t('validation.length', { length: Config.LENGTH_TASK_ATTACHMENT_CODE })
                : ''
            }
            onChange={onChangeHandler}
          />
        </>
      ) : (
        <>
          <IconButton id="editBtn" aria-label="edit" onClick={onEditHandler}>
            <EditIcon />
          </IconButton>
          <span>
            {referenceId}-{attachmentCode}
          </span>
        </>
      )}
    </>
  );
};

AttachmentCodeInput.propTypes = {
  attachmentCode: PropTypes.string.isRequired,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  onConfirm: PropTypes.func.isRequired,
};

AttachmentCodeInput.defaultProps = {};

export default AttachmentCodeInput;
