import { getDefaultOptions, request, requestBlob } from 'api/helpers';

export const NEXTCLOUD_API = 'nextcloud';
export const ADHOC_API = 'adhoc';
const SEARCH_ADHOC_API = 'adhoc/search';
const DOWNLOAD_ADHOC_API = 'adhoc/download';
const ALL_TYPES_API = 'all/types';
const referenceTypeRequestParameter = 'referenceType=';

export const apiAttachmentPost = async (url, body) => {
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: body ? JSON.stringify(body) : null,
  };
  return request(url, options);
};

export const apiAttachmentDelete = async (serviceUrl, attachmentId) => {
  const url = `${serviceUrl}/${attachmentId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};

export const apiAttachmentGet = async (serviceUrl, referenceId, referenceType) => {
  const url = `${serviceUrl}/${referenceId}?${referenceTypeRequestParameter}${referenceType}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiAttachmentSearchAdhoc = async (serviceUrl, adhocSearchDto) => {
  const url = `${serviceUrl}/${SEARCH_ADHOC_API}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: adhocSearchDto ? JSON.stringify(adhocSearchDto) : null,
  };
  return request(url, options);
};

export const apiDownloadAdhocFile = async (serviceUrl, downloadDto, previewPdf) => {
  const url = `${serviceUrl}/${DOWNLOAD_ADHOC_API}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: downloadDto ? JSON.stringify(downloadDto) : null,
    responseType: 'blob',
  };
  return requestBlob(url, options, previewPdf);
};

export const apiAttachmentGetAllReferenceAndCloudType = async serviceUrl => {
  const url = `${serviceUrl}/${ALL_TYPES_API}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiUpdateAttachmentCode = async (
  serviceUrl,
  attachmentId,
  attachmentCode,
  referenceId
) => {
  const url = `${serviceUrl}/${attachmentId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: JSON.stringify({
      referenceId,
      code: attachmentCode,
    }),
  };
  return request(url, options);
};
