import ActionContext from 'context/action/ActionContext';
import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import {
  apiDeleteTask,
  apiTaskAction,
  apiTaskDetail,
  apiTaskUpdatePut,
  apiUpdateDatesTask,
  apiUpdateStatusTask,
} from '../../api/task';
import { getUniqueUsers, ROLES } from '../../helpers/utils';
import loadingManager from '../loading/LoadingManager';
import UserManager from '../user/UserManager';
import TaskContext from './TaskContext';

const TaskManager = () => {
  const [state, setState] = useContext(TaskContext);
  const [actionState, setActionState] = useContext(ActionContext);

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  const { setLoading } = loadingManager();
  const { setUsersPresent } = UserManager();
  const { t } = useTranslation();
  const { serviceUrl: taskServiceUrl } = state;
  const { serviceUrl: actionServiceUrl } = actionState;

  const fetchTask = async taskId => {
    try {
      setLoading(true);
      const json = await apiTaskDetail(taskServiceUrl, taskId);
      const { payload } = json;
      setState(prev => {
        return {
          ...prev,
          task: payload,
        };
      });
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const updateTask = async newTask => {
    try {
      setLoading(true);
      const result = await apiTaskUpdatePut(taskServiceUrl, newTask);
      console.log('updateTask', result);
      if (result.payload) {
        const json = await apiTaskDetail(taskServiceUrl, newTask.id);
        const { payload, metadata } = json;
        const usersTask = [
          ...new Set(
            Array.from(ROLES).reduce((acc, key) => {
              if (payload[key]) {
                acc.push(payload[key]);
              }
              return acc;
            }, [])
          ),
        ];
        setUsersPresent(usersTask);
        setState(prev => {
          return {
            ...prev,
            task: payload,
            metadata,
            edoResponsible: payload.responsible,
            usersSelected: ROLES.reduce((acc, role) => {
              acc[role] = payload[role];
              return acc;
            }, {}),
            error: null,
          };
        });
      }
    } catch (e) {
      console.log('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const updateTaskStatus = async (taskId, status) => {
    try {
      setLoading(true);
      const response = await apiUpdateStatusTask(taskServiceUrl, taskId, { status });
      if (response.payload.id) {
        fetchTask(taskId);
        setState(prev => {
          return {
            ...prev,
            message: t('message.status'),
          };
        });
      }
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const fetchActions = async taskId => {
    try {
      const response = await apiTaskAction(actionServiceUrl, taskId);
      const usersActions = getUniqueUsers(response.payload);
      setUsersPresent(usersActions);
      const actions = response.payload.map((act, index) => ({ ...act, index }));
      setActionState(prev => ({ ...prev, actions, message: t('message.status') }));
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    }
  };

  const updateTaskDates = async (taskId, updatingDate, newDate) => {
    try {
      setLoading(true);
      const response = await apiUpdateDatesTask(taskServiceUrl, taskId, {
        [updatingDate]: newDate,
      });
      if (response.payload.id) {
        await fetchTask(taskId);
        await fetchActions(taskId);
        setState(prev => {
          return {
            ...prev,
            message: t('message.date'),
          };
        });
      }
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const deleteTask = async taskId => {
    try {
      setLoading(true);
      const response = await apiDeleteTask(taskServiceUrl, taskId);
      if (response.payload.id) {
        setState(prev => {
          return {
            ...prev,
            message: t('message.task.delete'),
          };
        });
      }
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.task.delete') }));
    } finally {
      setLoading(false);
    }
  };

  return {
    usersTask: state.usersTask,
    task: state.task,
    metadata: state.metadata,
    edoResponsible: state.edoResponsible,
    error: state.error,
    message: state.message,
    usersSelected: state.usersSelected,
    taskListPage: state.taskListPage,
    clearError,
    clearMessage,
    updateTask,
    updateTaskStatus,
    updateTaskDates,
    deleteTask,
    fetchTask,
  };
};

export default TaskManager;
