import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Paper,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import PropTypes from 'prop-types';
import React from 'react';
import Draggable from 'react-draggable';
import { useTranslation } from 'react-i18next';
import dialogUserStyle from './style/DialogConfirm';

const PaperComponent = props => (
  <Draggable handle="#draggable-dialog-users" cancel={'[class*="MuiDialogContent-root"]'}>
    {/* eslint-disable-next-line react/jsx-props-no-spreading */}
    <Paper {...props} />
  </Draggable>
);

const DialogConfirm = ({
  open,
  title,
  message,
  buttonCancelLabel,
  buttonConfirmLabel,
  onCancel,
  onConfirm,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();

  // eslint-disable-next-line no-unused-vars
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const css = dialogUserStyle();

  return (
    <Dialog
      data-testid="dialog-confirm"
      fullWidth
      maxWidth="sm"
      open={open}
      onClose={onCancel}
      PaperComponent={PaperComponent}
      disableEnforceFocus
      aria-labelledby="form-dialog-title"
      className={css.dialog}
    >
      <DialogTitle className={css.dialogTitle} id="form-dialog-title" onClose={onCancel}>
        <div>{title || t('common.confirmOperationTitle')}</div>
      </DialogTitle>
      <DialogContent>
        <DialogContentText>{message || t('common.confirmOperationMessage')}</DialogContentText>
      </DialogContent>
      <DialogActions className={css.dialogActions}>
        <Button
          onClick={onCancel}
          className={css.buttonClose}
          data-testid="dialog-confirm-close-button"
        >
          {buttonCancelLabel || t('common.cancel')}
        </Button>
        <Button onClick={onConfirm} className={css.button} data-testid="dialog-user-confirm-button">
          {buttonConfirmLabel || t('common.confirm')}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

DialogConfirm.propTypes = {
  open: PropTypes.bool,
  onCancel: PropTypes.func.isRequired,
  onConfirm: PropTypes.func.isRequired,
  title: PropTypes.string,
  message: PropTypes.string,
  buttonCancelLabel: PropTypes.string,
  buttonConfirmLabel: PropTypes.string,
};

DialogConfirm.defaultProps = {
  open: false,
  title: '',
  message: '',
  buttonCancelLabel: '',
  buttonConfirmLabel: '',
};

export default DialogConfirm;
