import DateFnsUtils from '@date-io/date-fns';
import { FormControl } from '@material-ui/core';
import { KeyboardDatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import itLocale from 'date-fns/locale/it';
import { isValidDate } from 'helpers/utils';
import { isEmpty } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import inputDatePicker from './style/inputDatePickerStyle';

const InputDatePicker = props => {
  const {
    name,
    errors,
    format,
    initialValue,
    minDate,
    maxDate,
    invalidDateMessage,
    onChange,
    disabled,
  } = props;

  const [value, setValue] = useState();

  const css = inputDatePicker();
  useEffect(() => {
    if (initialValue) {
      setValue(initialValue);
    }
    // eslint-disable-next-line
  }, [initialValue]);

  const innerOnChange = newDate => {
    if (isValidDate(newDate)) {
      if (onChange) {
        onChange(newDate);
      }
    }
  };

  return (
    <FormControl variant="outlined" fullWidth error={!isEmpty(errors[name])}>
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={itLocale}>
        <KeyboardDatePicker
          onChange={innerOnChange}
          className={css.datePicker}
          value={value}
          inputVariant="outlined"
          autoOk
          disableToolbar
          invalidDateMessage={invalidDateMessage}
          variant="inline"
          format={format}
          margin="normal"
          id="date-picker-inline"
          label=""
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
          fullWidth
          minDate={minDate}
          maxDate={maxDate}
          disabled={disabled}
          allowKeyboardControl={false}
        />
      </MuiPickersUtilsProvider>
    </FormControl>
  );
};

InputDatePicker.propTypes = {
  control: PropTypes.shape({}),
  initialValue: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  // value: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  // setValue: PropTypes.func.isRequired,
  onChange: PropTypes.func,
  rules: PropTypes.shape({}),
  errors: PropTypes.shape({}),
  name: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  format: PropTypes.string,
  minDate: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  maxDate: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  invalidDateMessage: PropTypes.string,
};

InputDatePicker.defaultProps = {
  control: {},
  errors: {},
  rules: {},
  onChange: () => {},
  format: 'dd/MM/yyyy',
  initialValue: undefined,
  disabled: false,
  minDate: undefined,
  maxDate: undefined,
  invalidDateMessage: 'Invalid Date Format',
};

export default InputDatePicker;
